<template>
  <footer class="control">
    <div class="progerss">
      <input
        type="range"
        min="0"
        :max="duration"
        @input="inputing = true"
        @change="changeValue"
        v-model="value"
      />
      <div class="line"></div>
      <div
        class="dot"
        :style="{
          left: ((inputing ? value : currentTime) / duration) * 100 + '%',
        }"
      ></div>
    </div>

    <ul class="icons">
      <li>
        <img src="../assets/icons/列表循环.png" style="" alt="">
      </li>
      <li @click="$emit('prev-song')" > 
       <img src="../assets/icons/上一曲 .png" alt="">
       </li>
      <li class="icon-open">
        <span class="play-icons"
        v-if="!playing" @click.stop="$emit('audio-play')">
        </span>
        <span class="paused-icons"
        v-else @click.stop="$emit('audio-pause')">
        </span>
      </li>

      <li @click="$emit('next-song')"> 
        <img src="../assets/icons/上一曲 .png" style="
        transform: rotate(180deg);" alt="">
       </li>
      <li @click="$emit('show-list')">
        <img src="../assets/icons/列表 .png"  alt="">
      </li>
    </ul>
  </footer>
</template>

<script>
export default {
  props: {
    duration: Number,
    currentTime: Number,
    playing: Boolean,
  },
  data: function () {
    return {
      inputing: false,
      value: this.currentTime,
    };
  },
  methods: {
    changeValue: function (e) {
      this.inputing = false;
      //   console.log(e.target.value);
      this.$emit("updata:currentTime", e.target.value);
    },
    
  },
};
</script>

<style lang="less">
.control {
  
  width: 100%;
  .progerss {
    margin-left: 10%;
    width: 80%;
    height: 10px;
    // background: lightcoral;

    position: relative;
    display: flex;
    align-items: center;

    input {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 1;
      opacity: 0;
    }
    .line {
      
      background: white;
      width: 100%;
      height: 2px;
      border-radius: 10px;
    }
    .dot {
      width: 10px;
      height: 10px;
      background: white;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: -5px;
      z-index: 0;
      box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
    }
  }
  .icons {
    align-items: center;
    box-sizing: border-box;
    margin:45px 0;
    display: flex;
    justify-content: space-around;
    li{
      img{
        width: 32px;
        height: 32px;
      }
      &.icon-open{
          border: 2px solid #ccc;
          border-radius: 50%;
          width: 50px;
          height: 50px;
          position: relative;
    span {
      width: 20px;
      height: 20px;
      &.paused-icons{
        &::after{
          width: 29px;
          height: 30px;
          display: block;
          content: '';
          border-left: 6px solid #ccc;
          border-right: 6px solid #ccc;
          transform: translate(30%,30%);
        }
      }
      &.play-icons {
        overflow: hidden;
        &::before {
          content: "";
          width: 29px;
          height: 29px;
          display: block;
          border-top: 15px solid transparent;
          border-right: 8px solid transparent;
          border-bottom: 15px solid transparent;
          border-left: 19px solid #ccc;
          position: absolute;
          top: 50%;
          left: 88%;
          transform: translate(-85%,-50%);
        }
      }
    }
  }
    }
  }
  }
</style>