<template>
  <section>
      <CommentHeader
      :detail="detail"
      
      />


    <ul v-if="detail">
      <MusicListItem
        @change-current-play-song="$emit('change-current-play-song', $event)"
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        :index="index"
      />
    </ul>

    <ul>
      <div class="pl-l">
        <i>精彩评论</i>
      </div>
      
      <CommentCard
        v-for="item in comments"
        :key="item.commentId"
        :item="item"
      />
    </ul>
    
  </section>
</template>

<script>
import MusicListItem from "../components/MusicListItem.vue";
import CommentCard from "../components/CommentCard.vue";
import CommentHeader from "@/components/CommentHeader.vue";
export default {
  components: {
    MusicListItem,
    CommentCard,
    CommentHeader,
  },
  data: function () {
    return {
      detail: null,
      comments: null,
      //personalized: [],
      //coverImgUrl:String,
    };
  },
  created() {
    //   获取歌单详情
    this.axios
      .get("https://music.kele8.cn/playlist/detail?id=" + this.$route.query.id)
      .then((res) => {
        this.detail = res.data.playlist;
      });

    // 获取歌单评论
    this.axios
      .get("https://music.kele8.cn/comment/playlist", {
        params: {
          id: this.$route.query.id,
        },
      })
      .then((res) => {
        this.comments = res.data.hotComments;
      });
      //歌单头部
      
  },
};
</script>

<style lang="less">
ul{
  //padding: 0 10px;
  .pl-l{
    //margin-top: 5px;
    padding: 0 10px;
    background-color: #ccc;
    i{
    font-size: 12px;
    font-style: normal;
  }
  }
}
</style>