<template>
  <section class="playbar">
    <img
      :src="`${
        currentPlaySong.picUrl || currentPlaySong.al.picUrl
      }?imageView=1&type=webp&thumbnail=60x0`"
      alt=""
    />
    <h5>{{ currentPlaySong.name }}</h5>
    <div class="progress">
      <span
        v-if="!playing"
        class="play-icon"
        @click.stop="$emit('audio-play')"
      ></span>
      <span v-else class="paused-icon" @click.stop="$emit('audio-pause')"
        ></span
      >
      <canvas ref="canvas" width="40" height="40"></canvas>
    </div>
    <div class="currentlist" @click.stop="$emit('show-list')">
      <img src="../assets/icons/列1.png" 
      style="width:30px; height:32px; margin:6px 0 0 7px;
      " alt="">
    </div>
  </section>
</template>

<script>
export default {
  props: {
    currentPlaySong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    circlePercent: Number,
  },

  methods: {
    drawCircle: function (n) {
      var canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");

      ctx.clearRect(0, 0, 40, 40);

      ctx.strokeStyle = "#16BD74";
      ctx.lineWidth="4"
      ctx.beginPath();
      ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI);
      // ctx.closePath();
      ctx.stroke();

      ctx.strokeStyle = "#ccc";

      ctx.beginPath();

      ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI, true);
      ctx.stroke();

      // console.log("xxxxxx", this);
    },
  },
  mounted() {
    this.drawCircle(this.circlePercent);
  },
  watch: {
    circlePercent: function (n, o) {
      // console.log(n);
      this.drawCircle(n);
    },
  },
};
</script>

<style lang="less" >
.playbar {
  width: 100%;
  height: 50px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px 10px;
  align-items: center;
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.15);

  img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
  }
  h5 {
    flex: 1;
    padding: 0 10px;
  }
  .progress {
    position: relative;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      width: 10px;
      height: 10px;
      &.paused-icon{
        &::after{
          width: 10px;
          height: 10px;
          display: block;
          content: '';
          border-left: 3px solid #16BD74;
          border-right: 3px solid #16BD74;
        }
      }
      &.play-icon {
        
        // background: red;
        overflow: hidden;
        &::before {
          content: "";
          width: 20px;
          height: 10px;
          display: block;
          // background: green;
          border-top: 5px solid transparent;
          border-right: 12px solid transparent;
          border-bottom: 5px solid transparent;
          border-left: 7px solid #ccc;
          margin-left: 3px;
        }
      }
    }

    canvas {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
    }
  }
.currentlist{
  
  
}
}
</style>