<template>
  <li
    class="current-play-list-item"
    @click="$emit('change-current-play-song', item)"
  >
    <div
      class="palying-animate"
      :class="{ paused: !playing }"
      v-if="currentPlaySong.id == item.id"
    >
      <span></span>
      <span></span>
      <span></span>
    </div>
   <div class="dtail-a" v-show="playing=true">
      <div class="detail">
      <span>{{ item.name }} - </span>

      <span class="artist" v-for="artist in item.song.artists" :key="artist.id">
        {{ artist.name }}
      </span>
    </div>
    <div class="delete">X</div>
   </div>

  <div class="dtail-b" v-show="playing=true">
      <div class="deta">
      <span>{{ item.al.name }} - </span>

      <span class="art" v-for="ar in item.ar.artists" :key="ar.id">
        {{ ar.name }}
      </span>
    </div>
    <div class="dele">X</div>
   </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
li.current-play-list-item {
  position: relative;
  display: flex;
  margin: 10px;
  @keyframes shensuo {
    from {
      height: 10%;
    }
    to {
      height: 100%;
    }
  }
  .palying-animate {
    width: 14px;
    height: 14px;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0 5px;
    span {
      width: 20%;
      height: 20%;
      background: red;
      animation: shensuo 0.6s linear infinite alternate;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.4s;
      }
    }
    &.paused {
      span {
        animation-play-state: paused;
      }
    }
  }
.dtail-a{
  display: flex;
  
    .detail {
      flex: 1;
     .artist{
       font-size: 12px;
       color: #c2c2c2;
     }
  }
  .delete {
      position: absolute;
      right: 4px;
      top: 2px;
      //margin: 0 4px;
    }
}
}
</style>