<template>
  <section>
    <SearchPage
     :suggests="suggests"
     @value-change="debouncedGetSuggests"
    />
    <SearchHot
    
    />
  </section>
</template>

<script>
import SearchPage from "@/components/SearchPage.vue";
 import SearchHot from "@/components/SearchHot.vue";
import debounce from "lodash/debounce";
// console.log(debounce);
export default {
  props:{
    currentPlaySong:null,
  },
components:{
  SearchPage,
  SearchHot,
},
data:function () {
  return{
    suggests:[],
  }
},
methods:{
  debouncedGetSuggests:function (n) {
    // console.log(n);获取搜索列表
    this.axios
    .get(
        "https://music.kele8.cn/search/suggest?keywords="+n+"&type=mobile"
      )
        .then((res) => {
          // return res;
          // console.log(res.data.result.allMatch);
          this.suggests = res.data.result.allMatch
        });
        this.axios
        .get("https://music.kele8.cn/search/hot")
        .then((res) => {
          //console.log(res.result.hots);
//this.suggests = res.data.result.hots
        })
    },
  },
  // created(){
  //   this.debouncedGetSuggests = debounce(this.getSuggests,500);
  // },
};
</script>

